//that is testing scss mixins, variables 
@import './mixins';
@import './variables';

//code
.container-box{
    margin: 2rem;
    @include border-box(2px, 2px, 5px, 5px, 2rem, $mainColor, grid, center);
    .container-buttons{
        display: flex;
        justify-content: center;
        .button-counter{
            margin: 0.5rem;
        }
        .btn{
            border-radius: 100%;
            width: 50px;
            height: 50px;
            &-lft{
                background-color: red;
                color: $whiteColor;
            }
            &-rgt{
                background-color: blue;
                color: $whiteColor;
            }
        }
    }
}

.home-page-container{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .h1-home-page{
        text-align: center;
    }
}

.wrapper-to-hamburger-menu{
    margin: 1rem;
}